import './style.css';
import React, { useState, useEffect } from 'react';
import Start from './Start';
import ReactDOM from 'react-dom';
import {filter} from './filter';
import {clean} from './filter';
import {hideAK} from './filter';
import {control} from './control-func';

const ControlPanel = () => {
  const [seconds, setSeconds] = useState(599);
  var [m, setM] = useState("10");
  var [s, setS] = useState("00");

  function reset() {
    setSeconds(600);
  }
  useEffect(() => {
    var sc = "";
    let interval = null;
    interval = setInterval(() => {
      setSeconds(seconds => seconds - 1);
      setM(m => m = Math.floor(seconds / 60));
      if((seconds % 60) < 10){
        sc = "0" + (seconds % 60)
      }
      else {sc = "" + (seconds % 60)}
      setS(s => s = sc);
    }, 1000)
    if (seconds === 0) {
      clearInterval(interval);
      ReactDOM.render(<Start />, document.getElementById('root'))
    }
    return () => clearInterval(interval);
  }, [seconds]);

  
  return (
    <div className="container page-panel page-adm">
      <div className="row">
        <div id="tim">Sesijos laikas: {m}:{s}</div>
        <div className="session"><button className="menubutton session" onClick={reset}>Atnaujinti sesija</button></div>
      </div>
      <div className="small-container">
        <div id="user" className="content">
          <div className="row">
            <div>
              <h2 className="flex-left" id="username">Valdymo skydas</h2>
            </div>
            <div className="flex-right">
              <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Atsijungti</button>
            </div>
          </div>
          <div>
            <div className="name">Vardas</div>
            <input id="name" placeholder="Vardas" className="name input-box" type="text" maxLength="15" />
          </div>
          <div>
            <div className="name">Pavardė</div>
            <input id="surname" placeholder="Pavardė" className="name input-box" type="text" maxLength="15" />
          </div>
          <div>
            <div className="name">Dienos</div>
            <input id="days" placeholder="Dienos" className="name input-box" type="text" maxLength="3" />
          </div>
          <div>
            <div className="name">Asmens kodas</div>
            <input id="code" placeholder="Asmens kodas" className="name input-box" type="text" maxLength="11" />
          </div>
          <div>
            <div className="name">Slaptažodis</div>
            <input id="password" placeholder="Slaptažodis" className="password input-box" type="password" maxLength="20" />
          </div>
          <div id="error"></div>
          <div className="buttons">
            <b className="btn-group">Vartotojas:</b>
            <button className="menubutton" onClick={() => control("User", "user", "add")}>Pridėti user</button>
            <button className="menubutton" onClick={() => control("User", "user", "upd")}>Atnaujinti user</button>
            <button className="menubutton" onClick={() => control("User", "user", "del")}>Pašalinti user</button>
            <button className="menubutton" onClick={() => control("User", "user", "res")}>Atstatyti user</button>
          </div>
          <div className="buttons">
            <b className="btn-group">Administratorius:</b>
            <button className="menubutton" onClick={() => control("Admin", "admin", "add")}>Pridėti admin</button>
            <button className="menubutton" onClick={() => control("Admin", "admin", "upd")}>Atnaujinti admin</button>
            <button className="menubutton" onClick={() => control("Admin", "admin", "del")}>Pašalinti admin</button>
            <button className="menubutton" onClick={() => control("Admin", "admin", "res")}>Atstatyti admin</button>
          </div>
          <div className="buttons">
            <b className="btn-group">Moderatorius:</b>
            <button className="menubutton" onClick={() => control("Admin", "mod", "add")}>Pridėti mod</button>
            <button className="menubutton" onClick={() => control("Admin", "mod", "upd")}>Atnaujinti mod</button>
            <button className="menubutton" onClick={() => control("Admin", "mod", "del")}>Pašalinti mod</button>
            <button className="menubutton" onClick={() => control("Admin", "mod", "res")}>Atstatyti mod</button>
          </div>
          <div className="buttons">
            <b className="btn-group">Kita:</b>
            <button className="menubutton" id="hid" onClick={() => hideAK()}>A.K.(paslėptas)</button>
            <button className="menubutton" onClick={() => clean()}>Išvalyti laukelius</button>
          </div>
          <h3 className="tag-margin">Filtruoti pagal:</h3>
          <div className="buttons">
            <button className="menubutton" onClick={() => filter("User", "name")}>Vardą</button>
            <button className="menubutton" onClick={() => filter("User", "surname")}>Pavardę</button>
            <button className="menubutton" onClick={() => filter("User", "days")}>Dienas</button>
            <button className="menubutton" onClick={() => filter("User", "code")}>Asmens kodą</button>
            <button className="menubutton" onClick={() => filter("User", "all")}>Rodyti visus</button>
            <button className="menubutton" onClick={() => filter("Admin", "admin")}>Administracija</button>
          </div>
          <div id="table" className="row">
            <div id="tc1" className="tc"><b>Vardas</b></div>
            <div id="tc2" className="tc"><b>Pavardė</b></div>
            <div id="tc3" className="tc"><b>Dienos</b></div>
            <div id="tc4" className="tc"><b>Asmens kodas</b></div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ControlPanel;