import firebase from 'firebase';

var vardai = "";
var pavardes = "";
var dienos = "";
var kodai = "";
var readloginRef = "";
var vardas = "";
var pavarde = "";
var diena = "";
var kodas = "";
var priv = "0";
var hid = 0;
export function filter(user, type){
    return(
        vardas = document.getElementById('name').value,
        pavarde = document.getElementById('surname').value,
        diena = document.getElementById('days').value,
        kodas = document.getElementById('code').value,
        vardai = "<br>",
        pavardes = "<br>",
        dienos = "<br>",
        kodai = "<br>",
        priv = "<br>",
        readloginRef = firebase.database().ref(user),
        readloginRef.on("value", function(snapshot){
            snapshot.forEach(function(childSnapshot){
                var data = childSnapshot.val();
                if (user === "User"){
                    
                    if((type === "name")&&(vardas === data.name)&&(data.status === "0")){
                        vardai = vardai + data.name + "<br>";
                        pavardes = pavardes + data.surname + "<br>";
                        dienos = dienos + data.days + "<br>";
                        if(hid === 1){
                            kodai = kodai + data.code + "<br>";
                        }
                        if(hid === 0){
                            kodai = kodai + "<i>paslėptas</i><br>";
                        }
                    }
                    if((type === "surname")&&(pavarde === data.surname)&&(data.status === "0")){
                        vardai = vardai + data.name + "<br>";
                        pavardes = pavardes + data.surname + "<br>";
                        dienos = dienos + data.days + "<br>";
                        if(hid === 1){
                            kodai = kodai + data.code + "<br>";
                        }
                        if(hid === 0){
                            kodai = kodai + "<i>paslėptas</i><br>";
                        }
                    }
                    if((type === "days")&&(diena === data.days)&&(data.status === "0")){
                        vardai = vardai + data.name + "<br>";
                        pavardes = pavardes + data.surname + "<br>";
                        dienos = dienos + data.days + "<br>";
                        if(hid === 1){
                            kodai = kodai + data.code + "<br>";
                        }
                        if(hid === 0){
                            kodai = kodai + "<i>paslėptas</i><br>";
                        }
                    }
                    if((type === "code")&&(kodas === data.code)&&(data.status === "0")){
                        vardai = vardai + data.name + "<br>";
                        pavardes = pavardes + data.surname + "<br>";
                        dienos = dienos + data.days + "<br>";
                        if(hid === 1){
                            kodai = kodai + data.code + "<br>";
                        }
                        if(hid === 0){
                            kodai = kodai + "<i>paslėptas</i><br>";
                        }
                    }
                    if((type === "all")&&(data.status === "0")){
                        vardai = vardai + data.name + "<br>";
                        pavardes = pavardes + data.surname + "<br>";
                        dienos = dienos + data.days + "<br>";
                        if(hid === 1){
                            kodai = kodai + data.code + "<br>";
                        }
                        if(hid === 0){
                            kodai = kodai + "<i>paslėptas</i><br>";
                        }
                    }
                }
                if ((user === "Admin")&&(type === "admin")){
                    if(data.status === "0"){
                        vardai = vardai + data.name + "<br>";
                        pavardes = pavardes + data.surname + "<br>";
                        if(data.permissions === "1"){
                            priv = priv + "Administratorius<br>";
                        }
                        if(data.permissions === "2"){
                            priv = priv + "Moderatorius<br>";
                        }
                        if(hid === 1){
                            kodai = kodai + data.code + "<br>";
                        }
                        if(hid === 0){
                            kodai = kodai + "<i>paslėptas</i><br>";
                        }
                    }
                }    
            })
            document.getElementById('tc1').innerHTML = "<b>Vardas</b>" + vardai;
            document.getElementById('tc2').innerHTML = "<b>Pavardė</b>" + pavardes;
            if(user === "User"){
                document.getElementById('tc3').innerHTML = "<b>Dienos</b>" + dienos;
            }
            if(user === "Admin"){
                document.getElementById('tc3').innerHTML = "<b>Privilegijos</b>" + priv;
            }
            document.getElementById('tc4').innerHTML = "<b>Asmens kodas</b>" + kodai;
        })
    )
}
export function clean(){
    return(
        document.getElementById('name').value = "",
        document.getElementById('surname').value = "",
        document.getElementById('days').value = "",
        document.getElementById('code').value = "",
        document.getElementById('password').value = ""
    )
}
export function hideAK(){
    var isHidden = document.getElementById('hid').innerHTML;
    if(isHidden === "A.K.(paslėptas)"){
        hid = 1;
        document.getElementById('hid').innerHTML = "A.K.(rodomas)";
    }
    else{
        hid = 0;
        document.getElementById('hid').innerHTML = "A.K.(paslėptas)";
    }
}