import firebase from 'firebase';
import ReactDOM from 'react-dom';
import User from './User';
import ControlPanel from './ControlPanel';
import ModPanel from './ModPanel';


var kodas = "";
var slaptazodis = "";
var readloginRef = "";
var id = "";
export function readLogin(list){
  return(
    kodas = document.getElementById('code').value,
    slaptazodis = document.getElementById('password').value,
    readloginRef = firebase.database().ref(list),
    readloginRef.on("value", function(snapshot){
        snapshot.forEach(function(loginSnapshot){
            var data = loginSnapshot.val();
            if((kodas === data.code)&&(slaptazodis === data.password)){
                id = loginSnapshot.key;
                localStorage.setItem('UserID', id);
                //document.getElementById("errorMsg").innerHTML = "";
                if(list === "User"){
                    ReactDOM.render(<User />, document.getElementById("root"));
                }
                if((list === "Admin")&&(data.permissions === "1")){
                    ReactDOM.render(<ControlPanel />, document.getElementById("root"));
                }
                if((list === "Admin")&&(data.permissions === "2")){
                    ReactDOM.render(<ModPanel />, document.getElementById("root"));
                }
            }
        })
        /*if(kodas === ""){
            document.getElementById("errorMsg").innerHTML = "Neįvestas asmens kodas";
        }
        if((kodas !== "")&&(slaptazodis === "")){
            document.getElementById("errorMsg").innerHTML = "Neįvestas slaptažodis";
        }
        else{
            document.getElementById("errorMsg").innerHTML = "Blogi prisijungimo duomenys";
        }*/
    })
  );
}

