import firebase from 'firebase';

var vardas = "";
var pavarde = "";
var dienos = "";
var kodas = "";
var slaptazodis = "";
var ID = "";
var check = 0;
var readControlRef = "";
var perm = "";
var formUser = "";
var newUser = "";
export function control(user, type, move) {
  return(
    ID = "",
    readControlRef = "",
    document.getElementById('error').innerHTML = "",
    vardas = document.getElementById('name').value,
    pavarde = document.getElementById('surname').value,
    dienos = document.getElementById('days').value,
    kodas = document.getElementById('code').value,
    slaptazodis = document.getElementById('password').value,
    readControlRef = firebase.database().ref(user),
    readControlRef.on("value", function(snapshot){
      snapshot.forEach(function(childSnapshot){
        var data = childSnapshot.val();
        if(kodas === data.code){
          ID = childSnapshot.key;
          check = check + 1;
          console.log(check);
        }
      })
      if((move === "add")&&(type === "user")&&(check === 0)){
        formUser = firebase.database().ref(user);
        newUser = formUser.push();
        newUser.set({
          name: vardas,
          surname: pavarde,
          days: dienos,
          code: kodas,
          password: slaptazodis,
          status: "0"
        });
        return false;
      }
      if((move === "add")&&(type !== "user")&&(check === 0)){
        formUser = firebase.database().ref(user);
        newUser = formUser.push();
        if(type === "admin"){
          perm = "1";
        }
        if(type === "mod"){
          perm = "2";
        }
        if(type !== "user"){
          newUser.set({
            name: vardas,
            surname: pavarde,
            code: kodas,
            password: slaptazodis,
            status: "0",
            permissions: perm
          });
        }
        document.getElementById('error').innerHTML = "Vartotojas pridetas";
        return false;
      }


      if((move === "upd")&&(check === 1)){
        var updateUser = firebase.database().ref(user + '/' + ID);
        if(type === "user"){
          updateUser.update({
            name: vardas,
            surname: pavarde,
            days: dienos,
            password: slaptazodis
          });
        }
        if(type !== "user"){
          updateUser.update({
            name: vardas,
            surname: pavarde,
            password: slaptazodis
          });
        }
        document.getElementById('error').innerHTML = "Vartotojas atnaujintas";
      }


      if((move === "del")&&(check === 1)){
        var delUser = firebase.database().ref(user + '/' + ID);
        delUser.update({
          status: "1"
        });
        document.getElementById('error').innerHTML = "Vartotojas ištrintas";
      }


      if((move === "res")&&(check === 1)){
        var resUser = firebase.database().ref(user + '/' + ID);
        resUser.update({
          status: "0"
        });
        document.getElementById('error').innerHTML = "Vartotojas atstatytas";
      }
      document.getElementById('tc1').innerHTML = "<b>Vardas</b>";
      document.getElementById('tc2').innerHTML = "<b>Pavardė</b>";
      document.getElementById('tc3').innerHTML = "<b>Dienos</b>";
      document.getElementById('tc4').innerHTML = "<b>Asmens kodas</b>";
      check = 0;
    })    
  );
}