import './style.css';
import React, { useState, useEffect } from 'react';
import Start from './Start';
import ReactDOM from 'react-dom';
import {filter} from './filter';
import {clean} from './filter';
import {hideAK} from './filter';
import {control} from './panel-func';
import {userSel} from './panel-func';
import {userGet} from './panel-func';

const Panel = () => {
  const [seconds, setSeconds] = useState(599);
  var [m, setM] = useState("10");
  var [s, setS] = useState("00");

  function reset() {
    setSeconds(600);
  }
  useEffect(() => {
    var sc = "";
    let interval = null;
    interval = setInterval(() => {
      setSeconds(seconds => seconds - 1);
      setM(m => m = Math.floor(seconds / 60));
      if((seconds % 60) < 10){
        sc = "0" + (seconds % 60)
      }
      else {sc = "" + (seconds % 60)}
      setS(s => s = sc);
    }, 1000)
    if (seconds === 0) {
      clearInterval(interval);
      ReactDOM.render(<Start />, document.getElementById('root'))
    }
    return () => clearInterval(interval);
  }, [seconds]);

  
  return (
    <div className="page-content">
      <div className="panel">
        <div className="contentp">
          <div className="row">
            <div id="tim" className="time">Sesijos laikas: {m}:{s}</div>
            <div className="session"><button className="menubutton" onClick={reset}>Atnaujinti sesija</button></div>
            <div className="session"><button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Atsijungti</button></div>
          </div>
          <div>
            <h2 id="username">Valdymo skydas</h2>
          </div>
          <h3 className="tag-margin">Filtravimas</h3>
          <div className="buttons filter">
            <button className="menubutton" onClick={() => filter("User", "name")}>Vardą</button>
            <button className="menubutton" onClick={() => filter("User", "surname")}>Pavardę</button>
            <button className="menubutton" onClick={() => filter("User", "days")}>Dienas</button>
            <button className="menubutton" onClick={() => filter("User", "code")}>Asmens kodą</button>
          </div>
          <div className="buttons filter">
            <button className="menubutton" id="hid" onClick={() => hideAK()}>A.K.(paslėptas)</button>
            <button className="menubutton" onClick={() => clean()}>Išvalyti laukelius</button>
            <button className="menubutton" onClick={() => filter("User", "all")}>Rodyti visus</button>
            <button className="menubutton" onClick={() => filter("Admin", "admin")}>Administracija</button>
          </div>
          <div id="table" className="row">
            <div id="tc1" className="tc"><b>Vardas</b></div>
            <div id="tc2" className="tc"><b>Pavardė</b></div>
            <div id="tc3" className="tc"><b>Dienos</b></div>
            <div id="tc4" className="tc"><b>Asmens kodas</b></div>
          </div>
          <div className="row">
            <div>
              <label>Vardas</label><br/>
              <input id="name" placeholder="Vardas" className="input-bx" type="text" maxLength="15" />
            </div>
            <div>
              <label>Pavardė</label><br/>
              <input id="surname" placeholder="Pavardė" className="input-bx" type="text" maxLength="15" />
            </div>
            <div>
              <label>Dienos</label><br/>
              <input id="days" placeholder="Dienos" className="input-bx" type="text" maxLength="3" />
            </div>
            <div>
              <label>Asmens kodas</label><br/>
              <input id="code" placeholder="Asmens kodas" className="input-bx" type="text" maxLength="11" />
            </div>
          </div>
          <div>
            <label>Slaptažodis</label><br/>
            <input id="password" placeholder="Slaptažodis" className="input-bx" type="password" maxLength="20" />
          </div>
          <h3 className="tag-margin">Veiksmai</h3>
          <div id="error"></div>
          <div className="buttons sel">
            {userGet()}
            <button className="menubutton" onClick={() => userSel("User")}>User</button>
            <button className="menubutton" onClick={() => userSel("Admin")}>Admin</button>
            <button className="menubutton" onClick={() => userSel("Mod")}>Mod</button>
          </div>
          <div className="buttons">
            <b className="btn-group">Administratorius:</b>
            <button className="menubutton" onClick={() => control("Admin", "admin", "add")}>Pridėti admin</button>
            <button className="menubutton" onClick={() => control("Admin", "admin", "upd")}>Atnaujinti admin</button>
            <button className="menubutton" onClick={() => control("Admin", "admin", "del")}>Pašalinti admin</button>
            <button className="menubutton" onClick={() => control("Admin", "admin", "res")}>Atstatyti admin</button>
          </div>
          <div className="buttons">
            <b className="btn-group">Moderatorius:</b>
            <button className="menubutton" onClick={() => control("Admin", "mod", "add")}>Pridėti mod</button>
            <button className="menubutton" onClick={() => control("Admin", "mod", "upd")}>Atnaujinti mod</button>
            <button className="menubutton" onClick={() => control("Admin", "mod", "del")}>Pašalinti mod</button>
            <button className="menubutton" onClick={() => control("Admin", "mod", "res")}>Atstatyti mod</button>
          </div>
          <div className="buttons">
            <b className="btn-group">Kita:</b>
            <button className="menubutton" id="hid" onClick={() => hideAK()}>A.K.(paslėptas)</button>
            <button className="menubutton" onClick={() => clean()}>Išvalyti laukelius</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Panel;