import './style.css';
import ReactDOM from 'react-dom';
import Start from './Start';
import {readLogin} from './login-func';

function Login() {
  return (
    <div className="container page-login">
      <div className="small-container">
        <div className="content">
          <h2>Prisijungimas</h2>
          <div>
            <label className="name">Asmens kodas</label>
            <input id="code" className="input-box" placeholder="Asmens kodas" type="text" maxLength="11"/>
          </div>
          <div>
            <label className="name">Slaptažodis</label>
            <input id="password" className="input-box pw" placeholder="Slaptažodis" type="password" maxLength="20"/>
          </div>   
          <div id="errorMsg"/>  
          <div className="buttons">
            <button id="autobutton" className="menubutton" onClick={() => readLogin("User")}>Prisijungti</button>
            <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Grįžti atgal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;