import './style.css';
import React, { useState, useEffect } from 'react';
import Start from './Start';
import ReactDOM from 'react-dom';
import {filter} from './filter';
import {clean} from './filter';
import {hideAK} from './filter';

const ModPanel = () => {
  const [seconds, setSeconds] = useState(599);
  var [m, setM] = useState("10");
  var [s, setS] = useState("00");

  function reset() {
    setSeconds(600);
  }
  useEffect(() => {
    var sc = "";
    let interval = null;
    interval = setInterval(() => {
      setSeconds(seconds => seconds - 1);
      setM(m => m = Math.floor(seconds / 60));
      if((seconds % 60) < 10){
        sc = "0" + (seconds % 60)
      }
      else {sc = "" + (seconds % 60)}
      setS(s => s = sc);
    }, 1000)
    if (seconds === 0) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [seconds]);

  return (
    <div className="container page-panel">
      <div className="row">
        <div id="tim">Sesijos laikas: {m}:{s}</div>
        <div className="session"><button className="menubutton" onClick={reset}>Atnaujinti sesija</button></div>
      </div>
      <div className="small-container">
        <div id="user" className="content">
          <div className="row">
            <div>
              <h2 className="flex-left" id="username">Valdymo skydas</h2>
            </div>
            <div className="flex-right">
              <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Atsijungti</button>
            </div>
          </div>
          <div>
            <div className="name">Vardas</div>
            <input id="name" placeholder="Vardas" className="input-box" type="text" maxLength="15" />
          </div>
          <div>
            <div className="name">Pavardė</div>
            <input id="surname" placeholder="Pavardė" className="input-box" type="text" maxLength="15" />
          </div>
          <div>
            <div className="name">Dienos</div>
            <input id="days" placeholder="Dienos" className="input-box" type="text" maxLength="3" />
          </div>
          <div>
            <div className="name">Asmens kodas</div>
            <input id="code" placeholder="Asmens kodas" className="input-box pw" type="text" maxLength="11" />
            <div id="password"></div>
          </div>
          <div className="buttons">
            <button className="menubutton" onClick={() => clean()}>Išvalyti laukelius</button>
          </div>
          <h3 className="tag-margin">Filtruoti pagal:</h3>
          <div className="buttons">
            <button className="menubutton" onClick={() => filter("User", "name")}>Vardą</button>
            <button className="menubutton" onClick={() => filter("User", "surname")}>Pavardę</button>
            <button className="menubutton" onClick={() => filter("User", "days")}>Dienas</button>
            <button className="menubutton" onClick={() => filter("User", "code")}>Asmens kodą</button>
            <button className="menubutton" onClick={() => filter("User", "all")}>Rodyti visus</button>
            <button id="hid" className="menubutton" onClick={() => hideAK()}>A.K.(paslėptas)</button>
          </div>
          <div id="table" className="row">
            <div id="tc1" className="tc"></div>
            <div id="tc2" className="tc"></div>
            <div id="tc3" className="tc"></div>
            <div id="tc4" className="tc"></div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ModPanel;