import './style.css';
import React, { useState, useEffect } from 'react';
import Start from './Start';
import ReactDOM from 'react-dom';

const User = () => {
  const [seconds, setSeconds] = useState(599);
  var [m, setM] = useState("10");
  var [s, setS] = useState("00");

  function reset() {
    setSeconds(600);
  }
  useEffect(() => {
    var sc = "";
    let interval = null;
    interval = setInterval(() => {
      setSeconds(seconds => seconds - 1);
      setM(m => m = Math.floor(seconds / 60));
      if((seconds % 60) < 10){
        sc = "0" + (seconds % 60)
      }
      else {sc = "" + (seconds % 60)}
      setS(s => s = sc);
    }, 1000)
    if (seconds === 0) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [seconds]);


  //var id = localStorage.getItem('userID');
  //document.getElementById("username-u").innerHTML = "Vartotojas: " + id;
  return (
    <div className="container page-user">
      <div className="row">
        <div id="tim">Sesijos laikas: {m}:{s}</div>
        <div className="session"><button className="menubutton" onClick={reset}>Atnaujinti sesija</button></div>
      </div>
      <div className="small-container">
        <div id="user" className="content">
          <div className="row">
            <div className="flex-left">
              <h2 id="username-u">Vartotojas</h2>
            </div>
            <div className="flex-right">
              <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Atsijungti</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default User;